.GraphContainer {
    display: flex;
    width: 100%;
    padding: 24px;
    background: #EBF5FF;
    flex-direction: column;
    gap: 24px;
    margin-bottom: 20px;
    overflow-y: scroll ;
}

.InnerContainer {
    display: flex;
    flex-direction: row;
}

.Title {
    color: #333333;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
}

.DeviceContainer {
    display: flex;
    flex-direction: row;
    gap: 12px;
    width: 100%;
    align-items: center;
}

.BarChartContainer {
    width: 400px;
    height: 600px;
    background-color: white;
    padding: 12px;
    border-radius: 8px;
    border: 1px solid #E4E4E4;
    margin-right: 24px;
}

